import { Button, Checkbox, Form, Input, Select,Radio} from 'antd';
import './contactlist.scss';
import { useContext } from 'react';
import MyContext from "../../context"; 
import {useNavigate} from 'react-router-dom'
import {onFinishFailed,onReset,SuccessNotification } from '../../utils';
import { v4 as uuidv4 } from 'uuid';
const Contacts=()=>{
  const navigate=useNavigate()
    const [form] = Form.useForm();
    const {contactdata,setContactdata} = useContext(MyContext);
 const onFinish = (values) => {
  const id = uuidv4();
  const newFormValue = {...values,id:id}
  setContactdata([...contactdata,newFormValue]);
SuccessNotification();
navigate('/')
}
  
 
    return(<div className='newcontacts'>
      
        <Form className='newform'
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 16,
        }}
        style={{
          maxWidth: 600,
        }}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <div className='newcontactheader'><h2>Yeni laq Formu</h2></div>
         
        <Form.Item
          label="Ad"
          name="name"
          rules={[
            {
              required: true,
              message: 'Ad daxil edin!',
            },
          ]}
        >
          <Input placeholder='Ad daxil edin' />
        </Form.Item>
    
        <Form.Item
          label="Soyad"
          name="surname"
          rules={[
            {
              required: true,
              message: 'Soyad daxil edin!',
            },
          ]}
        >
          <Input placeholder='Soyad daxil edin'/>
        </Form.Item>
        <Form.Item
          label="Ata ad"
          name="father"
          rules={[
            {
              required: true,
              message: 'Ata adn daxil edin!',
            },
          ]}
        >
          <Input placeholder='Ata adn daxil edin'/>
        </Form.Item>
       
        <Form.Item
          label="Email"
          name="email"
          rules={[
            {
              required: true,
              message: 'Email unvann daxil edin!',
              type:'email'
            },
          ]}
        >
          <Input placeholder='Email unvann daxil edin!'/>
        </Form.Item>
        <Form.Item
          label="lav mlumat"
          name="description"
          rules={[
            {
              required: true,
              message: 'lav mlumatlar daxil edin!',
              
            },
          ]}
        >
          <Input.TextArea placeholder='lav mlumatlar daxil edin!'/>
        </Form.Item>
        <Form.Item
          label="Ixtisas"
          name="profession"
          rules={[
            {
              required: true,
              message: 'Ixtisas daxil edin!',
            },
          ]}
        >
          <Select defaultValue='developer'>
          <Select.Option value="developer">Web Developer</Select.Option>
          <Select.Option value="muhasib">Muhasib</Select.Option>
          <Select.Option value="analitik">Data Analitik</Select.Option>
          <Select.Option value="operator">Operator</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label="Cins">
          <Radio.Group className='genderbutton' defaultValue='kii'>
            <Radio value="kii">Kii </Radio>
            <Radio value="qadn"> Qadin </Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item
          name="remember"
          valuePropName="checked"
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Checkbox>Yeniliklri xatirlat!</Checkbox>
        </Form.Item>
        <div className='contactbuttons'>
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
          
        >
          <Button type="primary" htmlType="reset">
            Sfrla
          </Button>
          
          
        </Form.Item>
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit">
            Tsdiq et
          </Button>
          
          
        </Form.Item>
        </div>
      </Form>
</div>    );
    
};export default Contacts